<template>
  <div class="top">
    <div class="logo" @click="newBlank">
      <img src="../../static/logo.png" alt="">
      <div class="word">
        <div>Sun's</div>
        <div>Blog</div>
      </div>
    </div>
    <div class="menu">
      <div class="list">
        <router-link to="/blog" tag="div">博客</router-link>
        <router-link to="/about" tag="div">关于我</router-link>
        <router-link to="/contact" tag="div">和我联系</router-link>
      </div>
    </div>
    <div class="search">
        <input type="text">
        <div>搜索</div>
    </div>
  </div>

</template>

<script>
  export default{
      methods:{
        newBlank: function (){
            window.open(location.href);
        }
      }
  }

</script>

<style lang="scss" scoped>
  @mixin prix($proprety,$value){
    -webkit-#{$proprety}:$value;
    -moz-#{$proprety}:$value;
    -ms-#{$proprety}:$value;
    #{$proprety}:$value;
  }
  %name{
    width: 30%;
    height: 100%;
  }
  .top{
    height: 125px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    @include prix(display,flex);
    @include prix(justify-content,space-around);
    @include prix(align-items,center);
    .logo{
      cursor: pointer;
      width:20%;
      @extend %name;
      @include prix(display,flex);
      @include prix(align-items,center);
      img{
        height: 80%;
      }
      .word{
        display: inline-block;
        height: 68px;
        font-weight: 700;
        font-size: 30px;
        font-family: "Open Sans", Helvetica, Arial, Verdana, sans-serif;
        text-align: left;
        margin-left:10px;
      }
    }
    .menu{
      @extend %name;
      width: 35%;
      padding-top: 43px;
      box-sizing: border-box;
      .list{
        height: 40px;
        border-bottom: 1px solid black;
        border-top:1px solid black;
        div{
          font-size: 14px;
          width: 30%;
          height: 100%;
          display: inline-block;
          line-height: 40px;
          cursor: pointer;
        }
        div:hover{
          color: green;
        }
      }
    }
    .search{
      @extend %name;
      @include prix(display,flex);
      @include prix(justify-content,center);
      @include prix(align-items,center);
      width: 22%;
      input{
          height: 25px;
        }
        div{
          display: inline-block;
          height: 31px;
          line-height: 31px;
          width: 31px;
          background-color: black;
          color: white;
          font-size: 12px;
          text-align: center;
          border-radius: 3px;
      }
    }
  }

</style>



