import Vue from 'vue'
import Router from 'vue-router'
import blog from '../components/blog'
import about from '../components/about'
import contact from '../components/contact'
import vue from '../components/vue'
import css from '../components/css'
import javascript from '../components/javascript'
import sassCompass from '../components/sass&compass'
import details from '../components/details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/blog'
    },
    { path: '/blog',
      name: 'blog',
      component: blog,
      redirect: '/blog/vue',
      children: [
        {
          path: '/blog/vue',
          name: 'vue',
          component: vue,
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/blog/vue/:id',
          component: details,
        },
        {
          path: '/blog/css',
          name: 'css',
          component: css,
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/blog/css/:id',
          component: details,
        },
        {
          path: '/blog/javascript',
          name: 'javascript',
          component: javascript,
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/blog/javascript/:id',
          component: details,
        },
        {
          path: '/blog/sassCompass',
          name: 'sassCompass',
          component: sassCompass,
          meta: {
            keepAlive: true
          }
        },
        {
          path: '/blog/sassCompass/:id',
          component: details,
        }
      ]
    },
    { path: '/about',
      name: 'about',
      component: about
    },
    { path: '/contact',
      name: 'contact',
      component: contact
    }
  ],
})



