<template>
  <div id="vue">
   <item :item="items"></item>
  </div>
</template>

<script>
  export default {
    data () {
          return {
          items: []
      }
    },
    mounted: function () {
      this.$http.get('../../static/vue.json').then(function (res){
        console.log(res.ok);
        if(!res.ok) return;
        this.items = res.body;
      }).catch(function (err){
        console.log(err);
      })
    }
  }
</script>

<style lang="scss" scoped>
  .item{
    margin-bottom:20px;
    .title{
      color: #9ec09f;
      font-size: 22px;
      font-family: 微软雅黑;
      line-height: 22px;
    }
    img{
      width: 95%;
      margin: 20px 0 10px;
    }
    .time{
      color: #a0a0a0;
      font-size: 14px;
      line-height: 14px;
      margin: 0 0 10px 0;
    }
    .describe{
      color: #3f3f3f;
      font-size: 14px;
      width: 100%;
    }

  }

</style>



