<template>
  <div class="detail">
    <div class="title">{{ title }}</div>
    <div class="date">发布日期: {{ date }}</div>
    <img src="../../static/vue.jpg"  alt="" width="80%">
    <div class="describe">{{ describe }}</div>
  </div>
</template>

<script>

  var title = null;
    export default {
        data (){
            return{
                title:'',
                describe:null,
                date:null
            }
        },
        beforeRouteEnter:function (to,from,next){
            title = to.params.id;
            next()
        },
        mounted:function(){
            this.title = title;
            var that = this;
            this.$http.get("../../static/detail.json").then(function(res){
                if(!res.ok) return;
                var items= res.body;
                items.forEach(function (i){
                  if(i.title === title){
                      that.describe = i.describe;
                      that.date = i.date;
                  }
                })
            })
        }

    }
</script>

<style lang="scss" scoped>
  .title{
    color: #9ec09f;
    font-size: 25px;
    font-family: 微软雅黑;
    line-height: 22px;
  }
  .date{
    height: 20px;
    font-size: 12px;
    color: #9ec09f;
    line-height: 20px;
    padding-top: 30px;
  }
  img{
    width: 95%;
    margin: 10px auto 10px;
  }
  .time{
    color: #a0a0a0;
    font-size: 14px;
    line-height: 14px;
    margin: 0 0 10px 0;
  }
  .describe{
    color: #3f3f3f;
    font-size: 14px;
    width: 100%;
  }


</style>



