<template>
  <div id="item">
    <div class="item" v-for="i in item">
      <div class="title">{{ i.title }}</div>
      <router-link :to="$route.path+'/'+i.title" tag="img" :src="i.imgUrl" ></router-link>
      <div class="time">发布日期：{{ i.date }} 分类：教程 </div>
      <div class="describe">{{ i.brief }}</div>
    </div>
  </div>
</template>

<script>
    export default {
        props: ['item']
    }
</script>

<style lang="scss" scoped>
  .item{
    margin-bottom:20px;
    .title{
      color: #9ec09f;
      font-size: 22px;
      font-family: 微软雅黑;
      line-height: 22px;
    }
    img{
      width: 95%;
      margin: 20px 0 10px;
    }
    .time{
      color: #a0a0a0;
      font-size: 14px;
      line-height: 14px;
      margin: 0 0 10px 0;
    }
    .describe{
      color: #3f3f3f;
      font-size: 14px;
      width: 100%;
    }

  }

</style>



